import request from './request'

// get ad list
export function getAlertAdList(adv_type = 0) {
  return request({
    method: 'get',
    url: '/api-admin/v1/advToken/alertlist',
    params: {
      adv_type
    }
  })
}

// add ad list
export function addAlertAd(data, adv_type = 0) {
  return request({
    method: 'post',
    url: '/api-admin/v1/advToken/alertlist/add',
    data: {
      adv_type,
      ...data
    }
  })
}

// update ad list
export function updateAlertAd(data) {
  return request({
    method: 'post',
    url: '/api-admin/v1/advToken/alertlist/update',
    data
  })
}
// delete ad
export function removeAlertAd(id) {
  return request({
    method: 'post',
    url: '/api-admin/v1/advToken/alertlist/remove',
    data: {
      id
    }
  })
}

